.App {
    text-align: center;
    font-family: 'Roboto Condensed', sans-serif;
}

.app-header {
    width: 100%;
    background-image: linear-gradient(rgba(0,0,0,0.5), rgba(0, 0, 0, 0.5)),
    url("./images/city.jpg");
    background-size: cover;
    height: 200px;
    display: flex;
    color: white;
    font-size: 32px;
    align-items: center;
    justify-content: center;
}

.profile-picture {
    height: 200px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover
}

.body {
    display: flex;
    justify-content: flex-start;
    margin: 0 5%;
}

.sidebar {
    min-width: 150px;
    width: 20%;
    margin: 90px 20px 20px;
    padding-bottom: 15px;
    text-align: left;
}

.menu-button {
    width: 150px;
    color: white;
    padding: 10px;
    text-align: center;
    background-color: #EF6C00;
    font-weight: bold;
    border-radius: 6px;
    border: none;
    outline: none;
    cursor: pointer;
    display: none;
}

.menu-button.active {
    background-color: rgb(192, 85, 0);
}

.menu-button img {
    width: 15px;
    float: right;
    transform: scaleX(-1);
}

.main-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.main-title {
    text-align: center;
    font-size: 24px;
    width:100%;
    font-weight: bold;
    margin: 20px 0;
}

.profile-container {
    width: 44%;
    margin:3%
}

.profile {
    border-bottom: 1px rgba(0,0,0,0.3) solid;
}

.profile img {
    width: 100%;
}

.profile-content {
    margin: auto;
    text-align: left;
    padding: 15px 0;
}

.profile-content-title {
    font-size: 22px;
    font-weight: bold;
}

.profile-content-info {
    list-style: none;
    margin: 10px 0;
    padding: 0;
}

.profile-content-info li {
    text-align: left;
    font-size: 12px;
    padding: 3px 0;
}

.profile-content-info li span {
    padding: 3px;
    font-weight: bold;
}

.profile-content-info li span:first-child {
    color: #389FBF;
}


.title {
    font-size: 20px;
}
.title h5 {
    margin: 10px 0;
}
.search-form {

}
.search-input {
    font-size: 14px;
    font-weight: bold;
    margin: 5px 0;
}

.search-input label {
    width: 100%;
    display: block;
    padding: 5px 0;
}

.search-input input[type="text"], .search-input select {
    width: 100%;
    height: 25px;
    outline: #EF6C00;
    border: 1px solid #EF6C00;
    padding-left: 10px;
    color: grey;
}

.search-input select {
    height: 29px;
    width: calc(100% + 14px);
}


.form-group {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-top: 5px;
}

.form-group label {
    padding-left: 5px;
    font-size: 10px;
}

.search-input input[type="radio"] {
    content: '';
    position: relative;
    display: inline-block;
    vertical-align: baseline;
    margin-right: 4px;
    box-sizing: border-box;
    border-radius: 50%;
    width: 22px;
    height: 22px;
    cursor: pointer;
}

input[type='radio']:after {
    width: 14px;
    height: 14px;
    border-radius: 15px;
    top: 0px;
    left: 0px;
    position: relative;
    background-color: white;
    content: '';
    display: inline-block;
    visibility: visible;
    border: 2px solid #EF6C00;
    padding: 2px;
}

input[type='radio']:before {
    content: '';
    display: inline-block;
    vertical-align: baseline;
    margin-right: 4px;
    box-sizing: border-box;
    background-color: white;
    border-radius: 50%;
    left: 0px;
    width: 22px;
    height: 22px;
    cursor: pointer;
    position: absolute;
    top: 0;
    border: solid 2px #EF6C00;
}

input[type='radio']:checked:after {
        width: 6px;
        height: 6px;
        border-radius: 15px;
        top: 4px;
        left: 4px;
        position: relative;
        background-color: #EF6C00;
        content: '';
        display: inline-block;
        visibility: visible;
}

.main-pagination {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    margin: 30px 15px 60px;
    align-items: center;
}

.pagination-entry {
    flex-grow: 1;
    margin: 0 5px;
    max-width: 50px;
    cursor: pointer;

}

.pagination-entry.active {
    border-bottom: 1px solid #389FBF;;
    margin-top: 5px;
    color: #EF6C00;
    padding-bottom: 5px;
}

.pagination-entry:hover {
    border-bottom: 1px solid #389FBF;
    margin-top: 5px;
    color: #EF6C00;
}

.pagination-handle {
    cursor: pointer;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;

}

footer {
    height: 100px;
}

@media only screen and (max-width: 760px) {
    .body {
        flex-wrap: wrap;
        margin: 0;
    }
    .profile-container {
        width:100%;
    }

    .profile-picture {
        height: 250px;
    }

    .sidebar {
        width: 100%;
        margin-top: 10px;
        text-align: left;
        border-bottom: 1px solid black;
    }
    .menu-button {
        display: block;
    }
    .title {
        display: none;
    }
    .profile-content-title {
        font-size: 25px;
    }
    .profile-content li {
        font-size: 14px
    }

    .sidebar-main {
        display: none;
    }

    .sidebar-main.show {
        display: block;
    }

    .search-input input {
        width: 80%;
        margin: auto;
    }
}
